import axios from 'axios';
import ElementUI from "element-ui";
const ajax = {
    // get
    get: function(url, params = {}) {
        return new Promise((resolve, reject) => {
            axios.get(url, {
                    params: params,
                }, {
                    withCredentials: false,
                })
                .then((response) => {
                    if (response.data.code == 200) {
                        resolve(response.data)
                    } else if (response.data.code == 1) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 2) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 3) {
                        ElementUI.Message({
                            message: ',!',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 4) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 5) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 6) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 7) {
                        ElementUI.Message({
                            message: 'banner5',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 8) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 9) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 10) {
                        ElementUI.Message({
                            message: '5',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 11) {
                        ElementUI.Message({
                            message: '10',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 12) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 14) {
                        ElementUI.Message({
                            message: '',
                            type: 'warning'
                        });
                        reject('error');
                    } else if (response.data.code == 15) {
                        resolve(response.data);
                    }

                })
                .catch((error) => {


                    reject(error);
                })
        })
    },
    // get
    imgget: function(url, params = {}) {
        return new Promise((resolve, reject) => {
            axios.get(url, {
                    params: params,
                    responseType: 'arraybuffer'
                })
                .then((response) => {
                    resolve(response.data)
                })
                .catch((error) => {

                    reject(error);
                })
        })
    },
    //post
    post: function(url, data) {
        return new Promise((resolve, reject) => {
            axios.post(url, data)
                .then((response) => {
                    if (response.status == 200) {
                        resolve(response.data)
                    }
                })
                .catch((error) => {

                    reject(error);
                })
        })
    },
    //post
    postSave: function(url, data = {}) {
        return new Promise((resolve, reject) => {
            axios.post(url, qs.stringify(data), {
                    'Content-Type': 'application/x-www-form-urlencoded'
                })
                .then((response) => {
                    if (response.data.code == 200) {
                        resolve(response.data)
                    } else if (response.data.code == 4) {
                        ElementUI.Message({
                            message: '!',
                            type: 'warning'
                        });
                    } else if (response.data.code == 413) {
                        ElementUI.Message({
                            message: '!',
                            type: 'warning'
                        });
                        reject('error');
                    }
                })
                .catch((error) => {

                    reject(error);
                })
        })
    },
    // 
    postImg: function(url, data = {}, config) {
        return new Promise((resolve, reject) => {
            axios.post(url, data, config)
                .then((response) => {
                    if (response.data.code == 200) {
                        resolve(response.data)
                    }
                })
                .catch((error) => {

                    reject(error);
                })
        })
    },
    //put
    put: function(url, data = {}) {
        return new Promise((resolve, reject) => {
            axios.get(url, data)
                .then((response) => {
                    if (response.data.code == 200) {
                        resolve(response.data)
                    }
                })
                .catch((error) => {

                    reject(error);
                })
        })
    },
    //delete
    delete: function(url, data = {}) {
        return new Promise((resolve, reject) => {
            axios.get(url, data)
                .then((response) => {
                    if (response.data.code == 200) {
                        resolve(response.data)
                    }
                })
                .catch((error) => {

                    reject(error);
                })
        })
    },
}
export default ajax;